import React from "react";
import styled from "styled-components";
import WidgetSelector from "./header/WidgetSelector";
import media from "../utils/media";
import { widgetList } from "../utils/consts";

const Wrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "Baloo Bhai", cursive;
  padding: 5px 5px;
`;

const Title = styled.div`
  font-weight: 800;
  font-size: 2rem;

  ${media.mobile`font-size: 1rem;`}
`;

const WidgetSelectors = styled.div`
  display: flex;
`;

const Warning = styled.div`
  display: none;
  justify-content: flex-end;
  align-items: center;
  font-size: 0.7rem;
  padding: 0.3rem;

  ${media.mobile`display: flex;`}
`;

const Icon = styled.i`
  font-size: 0.8rem;
  margin-right: 0.3rem;
`;

function Header() {
  return (
    <>
      <Warning>
        <Icon className="material-icons">screen_rotation</Icon>
        <div>We only support vertial screen. Please turn your device</div>
      </Warning>
      <Wrapper>
        <Title className="handle">STUDY SOUND</Title>
        <WidgetSelectors>
          {widgetList.map((item, i) => (
            <WidgetSelector key={i}>{item}</WidgetSelector>
          ))}
        </WidgetSelectors>
      </Wrapper>
    </>
  );
}

export default Header;
